import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './auth/auth.guard';
import { ProductListComponent } from './recipe/product-list/product-list.component';
import { ProductCreateComponent } from './recipe/product-create/product-create.component';


const routes: Routes = [
  { path: '', component: ProductListComponent },
  { path: 'create', component: ProductCreateComponent, canActivate: [ AuthGuard ] },
  { path: 'edit/:recipeId', component: ProductCreateComponent, canActivate: [ AuthGuard ] },
  { path: 'auth', loadChildren: './auth/auth.module#AuthModule' },
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
  providers: [AuthGuard]
})
export class AppRoutingModule { }
