import { Component, OnInit } from '@angular/core';
import { PageEvent } from '@angular/material';
import { Subscription } from 'rxjs';
import { AuthService } from 'src/app/auth/auth.service';
import { Product } from '../product.model';
import { ProductService } from '../product.service';

@Component({
  selector: 'app-product-list',
  templateUrl: './product-list.component.html',
  styleUrls: ['./product-list.component.scss']
})
export class ProductListComponent implements OnInit {

  products: Product[] = [];
  isLoading = false;
  subscription: Subscription;
  authStatusSubscription: Subscription;
  totalpostsLength = 0;
  postsPerPage = 10;
  currentPage = 1;
  pageSizeOptions = [ 10, 20, 50 ];
  isAuthenticated = false;
  userId: string

  constructor(private productService: ProductService, private authService: AuthService) { }

  ngOnInit() {
    this.isLoading = true;
    this.productService.getProducts(this.postsPerPage, this.currentPage);
    this.isAuthenticated = this.authService.getIsAuthenticated();
    this.userId = this.authService.getUserId();
    this.authStatusSubscription = this.authService.getAuthStatusListener().subscribe((data: boolean) =>{
      this.isAuthenticated = data;
      this.userId = this.authService.getUserId();
    });
    this.subscription = this.productService.getProductUpdateListener()
      .subscribe((postData: {products: Product[], totCount: number}) => {
        this.products = postData.products;
        this.totalpostsLength = postData.totCount;
        this.isLoading = false;
      });
  }

  onDelete(productId: string){
    this.isLoading = true;
    this.productService.deleteProduct(productId).subscribe(() => {
      this.productService.getProducts(this.postsPerPage, this.currentPage);
    }, () => {
      this.isLoading = false;
    });
  }

  onPageChanged(pageChanged: PageEvent){
    this.isLoading = true;
    this.currentPage = pageChanged.pageIndex + 1;
    this.postsPerPage = pageChanged.pageSize;
    this.productService.getProducts(this.postsPerPage, this.currentPage);
  }

  ngOnDestroy(){
    this.subscription.unsubscribe();
    this.authStatusSubscription.unsubscribe();
  }

}
