import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { AuthData } from './auth-data.model';
import { environment } from 'src/environments/environment';

const BACKEND_URL = environment.BASE_URL + '/api/user';

@Injectable({
  providedIn: 'root'
})
export class AuthService {

  private token: string;
  private authStatusListener = new Subject<boolean>();
  private tokenTimer: any;
  private isAuthenticated: boolean;
  private userId: string;

  constructor(private http: HttpClient, private router: Router) { }

  getToken(){
    return this.token;
  }

  getAuthStatusListener(){
    return this.authStatusListener.asObservable();
  }

  getIsAuthenticated(){
    return this.isAuthenticated;
  }

  getUserId(){
    return this.userId;
  }

  createUser(email: string, password: string){
    const authData: AuthData = { email: email, password: password };
    this.http.post(BACKEND_URL + '/signup', authData)
    .subscribe((response) => {
      this.router.navigate(['/auth/login']);
    }, (error) => {
      this.authStatusListener.next(false);
    });
  }

  login( email: string, password: string){
    const authData: AuthData = { email: email, password: password };
    this.http.post(BACKEND_URL + '/login', authData)
    .subscribe((response: { token: string, expiresIn: number, userId: string }) => {
      this.token = response.token;
      if(this.token){
        const expiresInDuration = response.expiresIn;
        this.setAuthTimer(expiresInDuration);
        const expirationDate = new Date((new Date()).getTime() + expiresInDuration * 1000);
        this.userId = response.userId;
        this.saveAuthData(this.token, expirationDate, this.userId);
        this.isAuthenticated = true;
        this.authStatusListener.next(true);
        this.router.navigate(['/']);
      }
    }, (error) => {
      this.authStatusListener.next(false);
    });
  }

  autoAuthUser() {
    const authInfo = this.getAuthData();
    if(!authInfo){
      return;
    }
    const now = new Date();
    const expiresIn = authInfo.expirationDate.getTime() - now.getTime();
    if(expiresIn > 0) {
      this.token = authInfo.token;
      this.userId = authInfo.userId;
      this.isAuthenticated = true;
      this.setAuthTimer(expiresIn / 1000);
      this.authStatusListener.next(true);
    }
  }

  private setAuthTimer(duration: number){
    this.tokenTimer = setTimeout(() => {
      this.logout();
    }, duration * 1000);
  }

  private saveAuthData(token: string, expirationData: Date, userId: string){
    localStorage.setItem('token', token);
    localStorage.setItem('expiresIn', expirationData.toISOString());
    localStorage.setItem('userId', userId);
  }

  private removeAuthData(){
    localStorage.removeItem('token');
    localStorage.removeItem('expiresIn');
    localStorage.removeItem('userId');
  }

  private getAuthData(){
    const token = localStorage.getItem('token');
    const userId = localStorage.getItem('userId');
    const expiresIn = localStorage.getItem('expiresIn');
    if(!token || !expiresIn){
      return;
    }
    return{
      token: token,
      expirationDate: new Date(expiresIn),
      userId: userId
    }
  }

  logout(){
    this.token = null;
    this.isAuthenticated = false;
    this.authStatusListener.next(false);
    this.userId = '';
    this.router.navigate(['/']);
    clearTimeout(this.tokenTimer);
    this.removeAuthData();
  }
}
