import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { Subject } from 'rxjs';
import { map } from 'rxjs/operators';
import { environment } from 'src/environments/environment';
import { Product } from './product.model';

@Injectable({
  providedIn: 'root'
})
export class ProductService {


  private products: Product[] = [];
  private productssUpdated = new Subject<{totCount: number, products: Product[]}>();

  constructor(private http: HttpClient, private router: Router) { }

  getProducts(pageSize: number, currentPage: number) {
    const queryParams = `?pagesize=${pageSize}&page=${currentPage}`;
    this.http.get<{count: number, products: any[]}>(`${environment.BASE_URL}/api/products${queryParams}`)
      .pipe(map((postData) => {
        return {
          count: postData.count,
          products: postData.products.map (product => {
            return {
              title: product.title,
              description: product.description,
              _id: product._id,
              imagePath: product.imagePath,
              creator: product.creator,
              price: product.price
            };
          })
        }
      }))
      .subscribe((transformedProducts) => {
        this.products = transformedProducts.products;
        this.productssUpdated.next({totCount: transformedProducts.count, products: [...this.products]});
      });
  }

  getProductUpdateListener() {
    return this.productssUpdated.asObservable();
  }

  getProduct(id: string){
    return this.http.get<Product>(`${environment.BASE_URL}/api/products/${id}`);
  }

  addProduct(title: string, description: string, price: number, image: File) {
    const postData = new FormData();
    postData.append('title', title);
    postData.append('description', description);
    postData.append('image', image, title);
    postData.append('price', price.toString());

    const post: Product = { _id: null, title: title, description: description, imagePath: null, price: null, creator: null };
    this.http.post<{ message: string, product: Product }>(`${environment.BASE_URL}/api/products`, postData)
      .subscribe((response) => {
        this.router.navigate(['/']);
      });
  }

  updateProduct(id: string, title: string, description: string, price: number, image: File | string) {
    let postData: Product | FormData;
    if(typeof(image) === 'object'){
      postData = new FormData();
      postData.append('_id', id);
      postData.append('title', title);
      postData.append('description', description);
      postData.append('image', image, title);
    }
    else{
      postData = { _id: id, title: title, description: description, imagePath: image, price: null, creator: null };
    }

    this.http.put<{ message: string }>(`${environment.BASE_URL}/api/products/${id}`, postData)
      .subscribe((response) => {
        console.log(response);
        this.router.navigate(['/'])
      });
  }

  deleteProduct(productId: string){
    return this.http.delete(`${environment.BASE_URL}/api/products/${productId}`);
  }
}
